import React,{ useState, useEffect } from 'react';

class DashBoardComponent extends React.Component {
    constructor(props){
        super();
      
    }
render(){
    return (
        <div>{
            this.props.usersList.map(function(key){
                return (
                    <div className="dashBoard">
      
                    <div className="NAme">id: {key.id}</div>
                    <div className="NAme2">Name {key.name}</div>
                    <div className="NAme">age: {key.age}</div>
                    <div className="NAme">gender: {key.gender}</div>
                    <div className="NAme2">email: {key.email}</div>
                    
                    <div className="view">View</div>
                </div>
                )
            })
        }
            
      
        </div>
    )
}

}
export default DashBoardComponent;