import React from 'react';
import {withRouter,Link, Redirect} from 'react-router-dom';

class LoginComponent extends React.Component{
 
    constructor (props){
        super(props)
        this.state = {
            userName:props.userName,
            password: props.password,
            DashBoard:"",
        }
        let userName;
        let password;

        this.state = {
            redirect: false
          }
    }

    setRedirect = () => {
        this.setState({
          redirect: true
        })
      }
      renderRedirect = () => {
        if (this.state.redirect) {
          return <Redirect to='/target' />
        }
      }
    
    submitClickHandler = (e)=>{
        
       if(this.userName === this.props.userName && this.password === this.props.password){
        this.props.loginSucess(true);
        window.location = '/Board';
       }else{
        window.location = '/login'; 
        
       }
    }
    userNameChandler = (e)=>{
        this.userName = e.target.value;
    }
    passwordChangeHandler = (e)=>{
        this.password = e.target.value;
    }
    render(){
        return(
            
            <div className="login-main">
                {this.renderRedirect()}
                <div className="login-inner">
                    <div className="login">Login</div>
                    <div className="userName">UserName: <span><input type="test" onChange={this.userNameChandler}/></span></div>
                    <div className="password">Password: <span>  <input type="password" onChange={this.passwordChangeHandler}/></span></div>
                    <Link to="/Board">  <button className="submit-button" onClick={this.submitClickHandler}>Submit</button></Link>
                </div>
                
            </div>
        )
    }
}
export default LoginComponent;